/* ========== RESET GLOBAL ========== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  --imp-text-color: #ff3f81;
  --navbar-height: 120px; /* ✅ Altura total del navbar */
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0c0513;
  color: white;
  overflow-x: hidden;
}

.App {
  text-align: center;
  position: relative;
  min-height: 100vh;
}

/* ========== NAVBAR FIJO ========== */
.navbar {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  z-index: 9999 !important;
  padding: 0.3rem 2rem !important;
  font-size: 1.2rem !important;
  transition: all 0.3s ease-out 0s !important;
  height: 80px !important; /* + padding ya suma cerca de 120px */
  background-color: #181a27 !important;
}

.sticky {
  background-color: #1b1a2ea9 !important;
  backdrop-filter: blur(15px) !important;
  box-shadow: 0px 10px 10px 0px rgba(9, 5, 29, 0.171) !important;
}

/* ========== SECCIONES (MISMA DISTANCIA DESDE LA NAVBAR) ========== */
/* ✅ Altura compensada para TODAS las páginas (desktop) */
.home-section {
  padding-top: 200px !important;
}

.project-section {
  padding-top: 4000px !important;
}

.about-section {
  padding-top: 2100px !important;
}

.resume-section {
  padding-top: 2400px !important;
}

.contact-section {
  padding-top: 550px !important;
}

.section {
  padding-top: 500px !important;
}


/* ✅ Compensación extra en móviles */
@media (max-width: 768px) {
  .home-section {
    padding-top: 400px !important; /* Ajusta según necesites */
  }

  .project-section {
    padding-top: 640px !important; /* Ajuste para la sección de proyectos */
  }

  .about-section {
    padding-top: 1460px !important; /* Ajuste para About */
  }

  .resume-section {
    padding-top: 4050px !important; /* Ajuste para Resume */
  }

  .contact-section {
    padding-top: 640px !important; /* Ajuste para Contact */
  }

  .section {
    padding-top: 120px !important; /* Ajuste genérico para otras secciones */
  }
}



/* ✅ Fondos según sección */
.home-section {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom left,
    rgba(17, 16, 16, 0.678),
    rgba(12, 10, 22, 0.863)
  ) !important;
}

.project-section,
.about-section,
.contact-section,
.section {
  background-image: none !important;
  background-color: transparent !important;
}

.resume-section {
  background-image: none !important;
  background-color: transparent !important;
}

/* ========== CONTAINER UNIVERSAL ========== */
.container {
  max-width: 10000px;
  margin: 0 auto;
  padding: 0 2rem;
  width: 100%;
}

/* ✅ HOME - diseño especial */
.home-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 3rem;
  min-height: calc(100vh - var(--navbar-height));
  padding: 2rem 0;
}

.home-content {
  text-align: left;
  padding: 2rem 0;
}

.home-content h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}
.home-content h2 {
  font-size: 2rem;
  margin-bottom: 2rem;
  background: linear-gradient(135deg, #ff3f81, #ff69a3);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.home-content p {
  font-size: 1.2rem;
  color: #c9a9dd;
  line-height: 1.6;
  margin-bottom: 2rem;
}

/* ========== RESPONSIVE ========== */
@media (max-width: 768px) {
  .navbar {
    height: 90px !important;
    padding: 1rem 1.5rem !important;
    font-size: 1.3rem !important;
  }

  :root {
    --navbar-height: 130px; /* Ajuste en móvil */
  }

  .home-container {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 2rem;
    min-height: calc(100vh - var(--navbar-height));
    padding: 1rem 0;
  }

  .container {
    padding: 0 1rem;
  }
}

@media (max-width: 480px) {
  :root {
    --navbar-height: 140px;
  }

  .container {
    padding: 0 0.5rem;
  }
}

/* ========== FONDO VANTA ========== */
#vanta-bg {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100vw !important;
  height: 100vh !important;
  z-index: -1 !important;
  pointer-events: none !important;
  opacity: 1 !important;
}

.purple {
  color: var(--imp-text-color) !important;
}

/* ========== SCROLLBAR PERSONALIZADO ========== */
::-webkit-scrollbar {
  width: 7px;
}
::-webkit-scrollbar-track {
  background: #2d1950;
}
::-webkit-scrollbar-thumb {
  background: rgba(255, 63, 129, 0.959);
  border-radius: 12px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 63, 129, 0.8);
  border-radius: 12px;
}
